import React from "react";
import { Link, useLocation } from "react-router-dom";
import { useAuth } from "../context/AuthContext";
import {
  AppBar,
  Toolbar,
  Typography,
  Button,
  Box,
  IconButton,
  Menu,
  MenuItem,
  Avatar,
  Divider,
  ListItemIcon,
  Badge,
  Slide,
  useScrollTrigger,
} from "@mui/material";
import {
  AccountBalanceWallet as WalletIcon,
  AccountCircle as UserIcon,
  ExitToApp as LogoutIcon,
  Home as HomeIcon,
  TrendingUp as InvestIcon,
  Payment as DepositIcon,
  MoneyOff as WithdrawIcon,
  Notifications as NotificationsIcon,
  Menu as MenuIcon,
} from "@mui/icons-material";

// Hide AppBar on scroll (smooth)
function HideOnScroll(props: any) {
  const { children } = props;
  const trigger = useScrollTrigger();

  return (
    <Slide appear={false} direction="down" in={!trigger}>
      {children}
    </Slide>
  );
}

const Navbar: React.FC = () => {
  const { user, logout, isAuthenticated } = useAuth();
  const location = useLocation();
  const [anchorEl, setAnchorEl] = React.useState<null | HTMLElement>(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] =
    React.useState<null | HTMLElement>(null);

  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);

  const handleProfileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };

  const handleMobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setMobileMoreAnchorEl(event.currentTarget);
  };

  const menuId = "primary-search-account-menu";
  const mobileMenuId = "primary-search-account-menu-mobile";

  const navItems = [
    { label: "Home", icon: <HomeIcon />, path: "/" },
    { label: "Invest", icon: <InvestIcon />, path: "/invest" },
    { label: "Deposit", icon: <DepositIcon />, path: "/deposit" },
    { label: "Withdraw", icon: <WithdrawIcon />, path: "/withdraw" },
  ];

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem component={Link} to="/profile" onClick={handleMenuClose}>
        <ListItemIcon>
          <UserIcon fontSize="small" />
        </ListItemIcon>
        My Profile
      </MenuItem>
      <MenuItem component={Link} to="/wallet" onClick={handleMenuClose}>
        <ListItemIcon>
          <WalletIcon fontSize="small" />
        </ListItemIcon>
        My Wallet
      </MenuItem>
      <Divider />
      <MenuItem
        onClick={() => {
          handleMenuClose();
          logout();
        }}
      >
        <ListItemIcon>
          <LogoutIcon fontSize="small" />
        </ListItemIcon>
        Logout
      </MenuItem>
    </Menu>
  );

  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: "top", horizontal: "right" }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: "top", horizontal: "right" }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
      sx={{
        "& .MuiPaper-root": {
          width: "100%",
          maxWidth: "100%",
          borderRadius: 0,
          paddingTop: 2,
        },
      }}
    >
      {navItems.map((item) => (
        <MenuItem
          key={item.path}
          component={Link}
          to={item.path}
          onClick={handleMobileMenuClose}
          selected={location.pathname === item.path}
        >
          <ListItemIcon>{item.icon}</ListItemIcon>
          {item.label}
        </MenuItem>
      ))}
      {isAuthenticated && (
        <MenuItem onClick={handleProfileMenuOpen}>
          <ListItemIcon>
            <UserIcon fontSize="small" />
          </ListItemIcon>
          Profile
        </MenuItem>
      )}
    </Menu>
  );

  return (
    <HideOnScroll>
      <AppBar
        position="sticky"
        sx={{
          backdropFilter: "blur(10px)",
          backgroundColor: "rgba(255, 255, 255, 0.8)",
          boxShadow: "0 2px 8px rgba(0,0,0,0.1)",
        }}
      >
        <Toolbar>
          {/* Brand Logo */}
          <Typography
            variant="h5"
            component={Link}
            to="/"
            sx={{
              mr: 2,
              display: { xs: "none", md: "flex" },
              fontWeight: 800,
              textDecoration: "none",
              background: "linear-gradient(to right, #1e3c72, #2a5298)",
              WebkitBackgroundClip: "text",
              WebkitTextFillColor: "transparent",
            }}
          >
            CryptoInvest
          </Typography>

          {/* Mobile Menu Icon */}
          <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
            <IconButton
              size="large"
              aria-label="open mobile menu"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              sx={{
                "&:hover": { transform: "scale(1.1)" },
                transition: "0.2s ease-in-out",
              }}
            >
              <MenuIcon />
            </IconButton>
          </Box>

          {/* Brand Logo (Mobile View) */}
          <Typography
            variant="h6"
            component={Link}
            to="/"
            sx={{
              mr: 2,
              display: { xs: "flex", md: "none" },
              flexGrow: 1,
              fontWeight: 700,
              textDecoration: "none",
              color: "inherit",
            }}
          >
            CryptoInvest
          </Typography>

          {/* Desktop Navigation Items */}
          <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" }, ml: 3 }}>
            {navItems.map((item) => (
              <Button
                key={item.path}
                component={Link}
                to={item.path}
                startIcon={item.icon}
                sx={{
                  mx: 1,
                  fontWeight: 600,
                  borderRadius: "20px",
                  position: "relative",
                  color:
                    location.pathname === item.path
                      ? "primary.main"
                      : "text.primary",
                  "&::after": {
                    content: '""',
                    position: "absolute",
                    bottom: 0,
                    left: "50%",
                    width: location.pathname === item.path ? "50%" : "0%",
                    height: "2px",
                    backgroundColor: "primary.main",
                    transition: "all 0.3s ease",
                    transform: "translateX(-50%)",
                  },
                  "&:hover::after": {
                    width: "50%",
                  },
                }}
              >
                {item.label}
              </Button>
            ))}
          </Box>

          {/* Profile / Auth Buttons */}
          <Box sx={{ display: "flex", alignItems: "center" }}>
            {isAuthenticated ? (
              <>
                <IconButton
                  size="large"
                  color="inherit"
                  sx={{ mr: 1, position: "relative" }}
                >
                  <Badge badgeContent={4} color="error">
                    <NotificationsIcon />
                  </Badge>
                </IconButton>

                <IconButton
                  size="large"
                  edge="end"
                  aria-label="account of current user"
                  aria-controls={menuId}
                  aria-haspopup="true"
                  onClick={handleProfileMenuOpen}
                  sx={{
                    "&:hover": { transform: "scale(1.05)" },
                    transition: "0.2s ease-in-out",
                  }}
                >
                  <Avatar
                    alt={user?.username}
                    src={user?.avatar}
                    sx={{
                      width: 36,
                      height: 36,
                      border: "2px solid #2a5298",
                    }}
                  >
                    {user?.username?.charAt(0).toUpperCase()}
                  </Avatar>
                </IconButton>
              </>
            ) : (
              <>
                <Button
                  component={Link}
                  to="/login"
                  color="inherit"
                  sx={{ mr: 1, fontWeight: 600 }}
                >
                  Login
                </Button>
                <Button
                  component={Link}
                  to="/register"
                  variant="contained"
                  sx={{
                    borderRadius: "20px",
                    background: "linear-gradient(to right, #1e3c72, #2a5298)",
                    color: "white",
                    fontWeight: 600,
                  }}
                >
                  Register
                </Button>
              </>
            )}
          </Box>
        </Toolbar>
        {renderMobileMenu}
        {renderMenu}
      </AppBar>
    </HideOnScroll>
  );
};

export default Navbar;
