import axios, { AxiosError } from "axios";
import { User, WithdrawalData } from "../types/User";

// Define the types for the data objects used in the API calls
interface LoginCredentials {
  email: string;
  password: string;
}

interface DepositData {
  amount: number;
  currency: string;
  // Add other properties of depositData if they exist
}

interface RegisterUserData {
  username: string;
  email: string;
  password: string;
  referredBy?: string;
}

// 1. Create a reusable axios instance
const api = axios.create({
  baseURL: "http://localhost:9001/api",
  headers: {
    "Content-Type": "application/json",
  },
});

// 2. Helper function to set the authorization header
const getAuthHeaders = (token: string) => ({
  headers: {
    Authorization: `Bearer ${token}`,
  },
});

// 3. Define all API functions using the new `api` instance
export const registerUser = async (userData: RegisterUserData) => {
  try {
    const response = await api.post("/users/register", userData);
    return response.data;
  } catch (error) {
    handleApiError(error);
  }
};

export const loginUser = async (credentials: LoginCredentials) => {
  try {
    const response = await api.post("/users/login", credentials);
    return response.data;
  } catch (error) {
    handleApiError(error);
  }
};

export const verifyToken = async (token: string) => {
  try {
    const response = await api.get("/auth/verify", getAuthHeaders(token));
    return response.data;
  } catch (error) {
    handleApiError(error);
  }
};

export const getUserDashboard = async (userId: number, token: string) => {
  try {
    const response = await api.get(
      `/users/${userId}/dashboard`,
      getAuthHeaders(token)
    );
    return response.data;
  } catch (error) {
    handleApiError(error);
  }
};

// In your api.ts file
// In your depositFunds function in the frontend
export const depositFunds = async (formData: FormData, token: string) => {
  const userId = formData.get("userId");
  if (!userId) {
    throw new Error("User ID not found in form data.");
  }

  try {
    const response = await api.post(
      `/users/deposit/${userId}`,
      formData,
      // Combine the headers correctly here
      {
        headers: {
          ...getAuthHeaders(token).headers, // Use spread syntax to merge the headers objects
          "Content-Type": "multipart/form-data",
        },
      }
    );
    return response.data;
  } catch (error) {
    handleApiError(error);
    throw error;
  }
};

export const getWalletAddresses = async (userId: number, token: string) => {
  try {
    const response = await api.get(
      `/users/${userId}/wallets`,
      getAuthHeaders(token)
    );
    return response.data;
  } catch (error) {
    handleApiError(error);
  }
};

// 4. Centralized error handling helper
function handleApiError(error: unknown) {
  if (axios.isAxiosError(error)) {
    const axiosError = error as AxiosError;
    if (axiosError.response) {
      // The request was made and the server responded with a status code
      // that falls out of the range of 2xx
      console.error("API Error Response:", axiosError.response.data);
      console.error("Status Code:", axiosError.response.status);
    } else if (axiosError.request) {
      // The request was made but no response was received
      console.error("API Error Request:", axiosError.request);
    } else {
      // Something happened in setting up the request that triggered an Error
      console.error("Error Message:", axiosError.message);
    }
  } else {
    console.error("Unexpected Error:", error);
  }
  // You can re-throw the error if you want the calling function to handle it
  // throw error;
}
export const withdrawFunds = async (
  userId: number,
  withdrawalData: WithdrawalData,
  token: string
): Promise<{ message: string }> => {
  const response = await axios.post(
    `/users/${userId}/withdraw`,
    withdrawalData,
    { headers: { Authorization: `Bearer ${token}` } }
  );
  return response.data;
};
export const updateUser = async (userData: Partial<User>): Promise<User> => {
  const response = await axios.put("/api/users", userData, {
    headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
  });
  return response.data;
};

interface InvestmentRequest {
  planId: number;
  amount: number;
  asset: string;
}

interface InvestmentRecord {
  id: string;
  createdAt: string;
  planName: string;
  amount: number;
  asset: string;
  status: "active" | "completed" | "cancelled";
  profit: number;
}

export const invest = async (data: InvestmentRequest): Promise<void> => {
  const response = await axios.post("/api/investments", data, {
    headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
  });
  return response.data;
};

export const getInvestmentHistory = async (): Promise<InvestmentRecord[]> => {
  const response = await axios.get("/api/investments/history", {
    headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
  });
  return response.data;
};
