import React, { useEffect, useState } from "react";
import { DashboardData } from "../types/User";
import { getUserDashboard } from "../api/api";
import { useAuth } from "../context/AuthContext";
import { Link } from "react-router-dom";
import {
  Box,
  Button,
  Typography,
  Container,
  CircularProgress,
  Alert,
  Card,
  CardContent,
  Grid,
  Stack, // Added Stack for better button grouping
} from "@mui/material";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";
import ShowChartIcon from "@mui/icons-material/ShowChart";
import TrendingUpIcon from "@mui/icons-material/TrendingUp";
import PaidIcon from "@mui/icons-material/Paid"; // For deposit
import SavingsIcon from "@mui/icons-material/Savings"; // For invest
import PersonAddAlt1Icon from "@mui/icons-material/PersonAddAlt1"; // For referral

const Dashboard: React.FC = () => {
  const { user, token, logout } = useAuth();
  const [dashboardData, setDashboardData] = useState<DashboardData | null>(
    null
  );
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");

  useEffect(() => {
    const fetchDashboardData = async () => {
      if (!user?.id || !token) {
        setLoading(false);
        return;
      }

      try {
        const data = await getUserDashboard(user.id, token.trim()); // Add trim() to remove any whitespace
        setDashboardData(data);
      } catch (err) {
        setError("Failed to load dashboard data");
        console.error("Dashboard error:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchDashboardData();
  }, [user, token]);

  if (loading) {
    return (
      <Container
        maxWidth="lg"
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          minHeight: "100vh",
        }}
      >
        <CircularProgress />
      </Container>
    );
  }

  if (error) {
    return (
      <Container maxWidth="lg" sx={{ mt: 4 }}>
        <Alert severity="error">{error}</Alert>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box
        sx={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          mb: 4,
        }}
      >
        <Typography variant="h4" component="h1" fontWeight="bold">
          Welcome, {user?.username || "User"}!
        </Typography>
        <Button variant="outlined" color="error" onClick={logout}>
          Logout
        </Button>
      </Box>

      <Grid container spacing={3} mb={4}>
        <Grid xs={12} md={4}>
          <Card elevation={3} sx={{ backgroundColor: "#e3f2fd", p: 2 }}>
            <CardContent>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  gap: 1,
                  mb: 1,
                }}
              >
                <AccountBalanceWalletIcon color="primary" />
                <Typography color="text.secondary" variant="subtitle1">
                  Balance
                </Typography>
              </Box>
              <Typography variant="h4" fontWeight="bold">
                ${dashboardData?.user.balance.toFixed(2)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid xs={12} md={4}>
          <Card elevation={3} sx={{ backgroundColor: "#e8f5e9", p: 2 }}>
            <CardContent>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  gap: 1,
                  mb: 1,
                }}
              >
                <ShowChartIcon color="success" />
                <Typography color="text.secondary" variant="subtitle1">
                  Return On Investment
                </Typography>
              </Box>
              <Typography variant="h4" fontWeight="bold" color="success.main">
                ${dashboardData?.roi.toFixed(2)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid xs={12} md={4}>
          <Card elevation={3} sx={{ backgroundColor: "#fff3e0", p: 2 }}>
            <CardContent>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  gap: 1,
                  mb: 1,
                }}
              >
                <TrendingUpIcon color="warning" />
                <Typography color="text.secondary" variant="subtitle1">
                  Total Value
                </Typography>
              </Box>
              <Typography variant="h4" fontWeight="bold">
                ${dashboardData?.totalValue.toFixed(2)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
      <Box sx={{ mt: 4 }}>
        <Typography variant="h5" component="h2" mb={2}>
          Quick Actions
        </Typography>

        <Stack direction={{ xs: "column", sm: "row" }} spacing={2} mb={4}>
          <Button
            component={Link}
            to="/deposit"
            variant="contained"
            size="large"
            startIcon={<PaidIcon />}
            fullWidth
            sx={{ py: 2 }}
          >
            Deposit
          </Button>

          <Button
            component={Link}
            to="/invest"
            variant="contained"
            color="secondary"
            size="large"
            startIcon={<SavingsIcon />}
            fullWidth
            sx={{ py: 2 }}
          >
            Invest
          </Button>

          <Button
            component={Link}
            to="/withdraw"
            variant="contained"
            color="warning"
            size="large"
            startIcon={<AccountBalanceWalletIcon />}
            fullWidth
            sx={{ py: 2 }}
          >
            Withdraw
          </Button>

          <Button
            component={Link}
            to="/referral"
            variant="contained"
            color="info"
            size="large"
            startIcon={<PersonAddAlt1Icon />}
            fullWidth
            sx={{ py: 2 }}
          >
            Referral
          </Button>
        </Stack>
      </Box>
    </Container>
  );
};

export default Dashboard;
