import React, { useState } from "react";
import { registerUser } from "../api/api";
import { useNavigate } from "react-router-dom";

const RegisterPage: React.FC = () => {
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password: "",
    referredBy: "",
  });
  const [error, setError] = useState("");
  const navigate = useNavigate();

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const response = await registerUser(formData);
      localStorage.setItem("token", response.token);
      navigate("/dashboard");
    } catch (err) {
      setError("Registration failed. Please try again.");
    }
  };

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      {/* Header */}
      <header className="bg-white shadow-sm sticky top-0 z-50">
        <nav className="container mx-auto px-4 sm:px-6 lg:px-8 py-4 flex justify-between items-center">
          <div className="flex items-center space-x-8">
            <a href="/" className="text-xl font-bold text-gray-900">
              CryptoInvest
            </a>
            <div className="hidden md:flex space-x-6">
              <a
                href="/home"
                className="text-gray-600 hover:text-gray-900 transition-colors duration-200 font-medium"
              >
                HOME
              </a>
              <a
                href="/invest"
                className="text-gray-600 hover:text-gray-900 transition-colors duration-200 font-medium"
              >
                INVEST
              </a>
              <a
                href="/deposit"
                className="text-gray-600 hover:text-gray-900 transition-colors duration-200 font-medium"
              >
                DEPOSIT
              </a>
              <a
                href="/withdraw"
                className="text-gray-600 hover:text-gray-900 transition-colors duration-200 font-medium"
              >
                WITHDRAW
              </a>
            </div>
          </div>
          <a
            href="/register"
            className="bg-blue-600 text-white font-semibold py-2 px-4 rounded-full hover:bg-blue-700 transition-colors duration-200"
          >
            REGISTER
          </a>
        </nav>
      </header>

      {/* Main Content Area */}
      <main className="flex-grow flex items-center justify-center py-16 px-4">
        <div className="bg-white shadow-xl rounded-2xl w-full max-w-lg p-8 sm:p-10 border border-gray-200">
          <div className="text-center mb-8">
            <h1 className="text-4xl font-extrabold text-gray-900 mb-2">
              Welcome!
            </h1>
            <p className="text-gray-600 text-lg">
              Create your account in seconds.
            </p>
          </div>
          {error && (
            <div className="bg-red-100 text-red-700 px-4 py-3 mb-6 rounded-lg text-sm font-medium animate-pulse border border-red-200">
              {error}
            </div>
          )}
          <form onSubmit={handleSubmit} className="space-y-6">
            <div>
              <label
                htmlFor="username"
                className="block text-sm font-medium text-gray-700 mb-1"
              >
                Username
              </label>
              <input
                type="text"
                name="username"
                id="username"
                value={formData.username}
                onChange={handleChange}
                required
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 transition duration-200"
                placeholder="Enter a username"
              />
            </div>
            <div>
              <label
                htmlFor="email"
                className="block text-sm font-medium text-gray-700 mb-1"
              >
                Email
              </label>
              <input
                type="email"
                name="email"
                id="email"
                value={formData.email}
                onChange={handleChange}
                required
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 transition duration-200"
                placeholder="hello@example.com"
              />
            </div>
            <div>
              <label
                htmlFor="password"
                className="block text-sm font-medium text-gray-700 mb-1"
              >
                Password
              </label>
              <input
                type="password"
                name="password"
                id="password"
                value={formData.password}
                onChange={handleChange}
                required
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 transition duration-200"
                placeholder="Create a strong password"
              />
            </div>
            <div>
              <label
                htmlFor="referredBy"
                className="block text-sm font-medium text-gray-700 mb-1"
              >
                Referral Code (optional)
              </label>
              <input
                type="text"
                name="referredBy"
                id="referredBy"
                value={formData.referredBy}
                onChange={handleChange}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 transition duration-200"
                placeholder="Your friend's code"
              />
            </div>
            <button
              type="submit"
              className="w-full bg-blue-600 text-white font-bold py-3 rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition duration-300"
            >
              Register Now
            </button>
          </form>
          <p className="text-center text-gray-600 text-sm mt-8">
            Already have an account?{" "}
            <a
              href="/login"
              className="text-blue-600 font-semibold hover:underline"
            >
              Log in
            </a>
          </p>
        </div>
      </main>

      {/* Footer */}
      <footer className="bg-gray-800 text-gray-300 py-12">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8 grid grid-cols-1 md:grid-cols-4 gap-12">
          {/* About Section */}
          <div className="md:col-span-2">
            <h3 className="text-xl font-bold text-teal-400 mb-4">
              CryptoInvest
            </h3>
            <p className="text-sm leading-relaxed max-w-sm">
              Your trusted platform for cryptocurrency investments and secure
              transactions.
            </p>
            <div className="flex space-x-4 mt-6">
              <a
                href="#"
                className="hover:text-white transition-colors duration-200"
              >
                <i className="fab fa-facebook-f"></i>
              </a>
              <a
                href="#"
                className="hover:text-white transition-colors duration-200"
              >
                <i className="fab fa-twitter"></i>
              </a>
              <a
                href="#"
                className="hover:text-white transition-colors duration-200"
              >
                <i className="fab fa-linkedin-in"></i>
              </a>
              <a
                href="#"
                className="hover:text-white transition-colors duration-200"
              >
                <i className="fab fa-instagram"></i>
              </a>
            </div>
          </div>

          {/* Quick Links */}
          <div>
            <h4 className="text-lg font-semibold text-white mb-4">
              Quick Links
            </h4>
            <ul className="space-y-2">
              <li>
                <a
                  href="/home"
                  className="hover:text-white transition-colors duration-200"
                >
                  Home
                </a>
              </li>
              <li>
                <a
                  href="/dashboard"
                  className="hover:text-white transition-colors duration-200"
                >
                  Dashboard
                </a>
              </li>
              <li>
                <a
                  href="/deposit"
                  className="hover:text-white transition-colors duration-200"
                >
                  Deposit
                </a>
              </li>
              <li>
                <a
                  href="/withdraw"
                  className="hover:text-white transition-colors duration-200"
                >
                  Withdraw
                </a>
              </li>
              <li>
                <a
                  href="/about"
                  className="hover:text-white transition-colors duration-200"
                >
                  About Us
                </a>
              </li>
            </ul>
          </div>

          {/* Legal */}
          <div>
            <h4 className="text-lg font-semibold text-white mb-4">Legal</h4>
            <ul className="space-y-2">
              <li>
                <a
                  href="/terms"
                  className="hover:text-white transition-colors duration-200"
                >
                  Terms of Service
                </a>
              </li>
              <li>
                <a
                  href="/privacy"
                  className="hover:text-white transition-colors duration-200"
                >
                  Privacy Policy
                </a>
              </li>
              <li>
                <a
                  href="/cookies"
                  className="hover:text-white transition-colors duration-200"
                >
                  Cookie Policy
                </a>
              </li>
              <li>
                <a
                  href="/aml"
                  className="hover:text-white transition-colors duration-200"
                >
                  AML Policy
                </a>
              </li>
            </ul>
          </div>

          {/* Contact Us */}
          <div>
            <h4 className="text-lg font-semibold text-white mb-4">
              Contact Us
            </h4>
            <ul className="space-y-2">
              <li className="flex items-center">
                <i className="fas fa-envelope mr-3 text-teal-400"></i>
                <a
                  href="mailto:support@cryptoinvest.com"
                  className="hover:text-white transition-colors duration-200"
                >
                  support@cryptoinvest.com
                </a>
              </li>
              <li className="flex items-center">
                <i className="fas fa-phone mr-3 text-teal-400"></i>
                <a
                  href="tel:+15551234567"
                  className="hover:text-white transition-colors duration-200"
                >
                  +1 (555) 123-4567
                </a>
              </li>
              <li className="flex items-center">
                <i className="fas fa-map-marker-alt mr-3 text-teal-400"></i>
                <span>123 Crypto Street, Blockchain City</span>
              </li>
            </ul>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default RegisterPage;
