import React, { useState, useEffect } from "react";
import { useAuth } from "../context/AuthContext";
import { depositFunds, getWalletAddresses } from "../api/api";
import { DepositData, WalletInfo } from "../types/User";
import {
  validateBTCAddress,
  validateETHAddress,
  validateUSDTAddress,
} from "../utils/walletValidation";
import {
  Box,
  Typography,
  TextField,
  Button,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  Paper,
  Container,
  Alert,
  Divider,
  Link,
  InputAdornment,
  IconButton,
  FormHelperText,
} from "@mui/material";
import { SelectChangeEvent } from "@mui/material/Select";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import CloudUploadIcon from "@mui/icons-material/CloudUpload";
import { styled } from "@mui/material/styles";

const CURRENCY_INFO = {
  BTC: {
    name: "Bitcoin",
    website: "https://bitcoin.org",
    explorer: "https://www.blockchain.com/explorer",
  },
  ETH: {
    name: "Ethereum",
    website: "https://ethereum.org",
    explorer: "https://etherscan.io",
  },
  USDT: {
    name: "Tether",
    website: "https://tether.to",
    explorer: "https://etherscan.io", // For ERC20, would need TRON explorer for TRC20
  },
};

const VisuallyHiddenInput = styled("input")({
  clip: "rect(0 0 0 0)",
  clipPath: "inset(50%)",
  height: 1,
  overflow: "hidden",
  position: "absolute",
  bottom: 0,
  left: 0,
  whiteSpace: "nowrap",
  width: 1,
});

const DepositForm: React.FC = () => {
  const { user, token } = useAuth();
  const [depositData, setDepositData] = useState<DepositData>({
    amount: 0,
    currency: "BTC",
    walletAddress: "",
  });
  const [walletInfo, setWalletInfo] = useState<WalletInfo | null>(null);
  const [network, setNetwork] = useState<"ERC20" | "TRC20">("ERC20");
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [isValidAddress, setIsValidAddress] = useState(false);
  const [screenshot, setScreenshot] = useState<File | null>(null);
  const [copied, setCopied] = useState<{ [key: string]: boolean }>({
    BTC: false,
    ETH: false,
    USDT: false,
  });

  useEffect(() => {
    const fetchWalletAddresses = async () => {
      if (user && token) {
        try {
          const addresses = await getWalletAddresses(user.id, token);
          setWalletInfo(addresses);
        } catch (err) {
          setError("Failed to load wallet information");
        }
      }
    };

    fetchWalletAddresses();
  }, [user, token]);

  useEffect(() => {
    if (!depositData.walletAddress) {
      setIsValidAddress(false);
      return;
    }

    try {
      let valid = false;
      switch (depositData.currency) {
        case "BTC":
          valid = validateBTCAddress(depositData.walletAddress);
          break;
        case "ETH":
          valid = validateETHAddress(depositData.walletAddress);
          break;
        case "USDT":
          valid = validateUSDTAddress(depositData.walletAddress, network);
          break;
      }
      setIsValidAddress(valid);
    } catch (err) {
      setIsValidAddress(false);
    }
  }, [depositData.walletAddress, depositData.currency, network]);

  const handleCurrencyChange = (
    event: SelectChangeEvent<"BTC" | "ETH" | "USDT">
  ) => {
    setDepositData((prev) => ({
      ...prev,
      currency: event.target.value as "BTC" | "ETH" | "USDT",
      walletAddress: "", // Reset wallet address when currency changes
    }));
  };

  const handleNetworkChange = (event: SelectChangeEvent<"ERC20" | "TRC20">) => {
    setNetwork(event.target.value as "ERC20" | "TRC20");
    if (depositData.currency === "USDT") {
      setIsValidAddress(false); // Force revalidation of address
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setDepositData((prev) => ({
      ...prev,
      [name]: name === "amount" ? parseFloat(value) || 0 : value,
    }));
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files[0]) {
      setScreenshot(e.target.files[0]);
    }
  };

  const copyToClipboard = (text: string, currency: string) => {
    navigator.clipboard.writeText(text);
    setCopied({ ...copied, [currency]: true });
    setTimeout(() => setCopied({ ...copied, [currency]: false }), 2000);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setSuccess("");

    if (!isValidAddress) {
      setError("Please enter a valid wallet address");
      return;
    }

    if (!screenshot) {
      setError("Please upload a screenshot of the deposit confirmation");
      return;
    }

    try {
      if (user && token) {
        const formData = new FormData();
        formData.append("userId", user.id.toString());
        formData.append("amount", depositData.amount.toString());
        formData.append("currency", depositData.currency);
        formData.append("walletAddress", depositData.walletAddress);

        if (depositData.currency === "USDT") {
          formData.append("network", network);
        }

        formData.append("screenshot", screenshot);

        // Now calling with correct arguments
        await depositFunds(formData, token);

        setSuccess(
          `Deposit of ${depositData.amount} ${depositData.currency} submitted successfully! Our team will verify your deposit shortly.`
        );

        // Reset form
        setDepositData({
          amount: 0,
          currency: depositData.currency,
          walletAddress: "",
        });
        setScreenshot(null);
      }
    } catch (err) {
      setError(
        err instanceof Error ? err.message : "Deposit failed. Please try again."
      );
    }
  };

  const getExplorerLink = () => {
    if (depositData.currency === "USDT" && network === "TRC20") {
      return "https://tronscan.org";
    }
    return CURRENCY_INFO[depositData.currency].explorer;
  };

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography
          variant="h4"
          component="h1"
          gutterBottom
          sx={{ fontWeight: "bold" }}
        >
          Make a Deposit
        </Typography>
        <Divider sx={{ my: 3 }} />

        {error && (
          <Alert severity="error" sx={{ mb: 3 }}>
            {error}
          </Alert>
        )}
        {success && (
          <Alert severity="success" sx={{ mb: 3 }}>
            {success}
          </Alert>
        )}

        <Box component="form" onSubmit={handleSubmit} noValidate>
          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel id="currency-label">Currency</InputLabel>
            <Select
              labelId="currency-label"
              id="currency"
              value={depositData.currency}
              label="Currency"
              onChange={handleCurrencyChange}
            >
              <MenuItem value="BTC">Bitcoin (BTC)</MenuItem>
              <MenuItem value="ETH">Ethereum (ETH)</MenuItem>
              <MenuItem value="USDT">Tether (USDT)</MenuItem>
            </Select>
          </FormControl>

          {depositData.currency === "USDT" && (
            <FormControl fullWidth sx={{ mb: 3 }}>
              <InputLabel id="network-label">Network</InputLabel>
              <Select
                labelId="network-label"
                value={network}
                label="Network"
                onChange={handleNetworkChange}
              >
                <MenuItem value="ERC20">ERC20 (Ethereum Network)</MenuItem>
                <MenuItem value="TRC20">TRC20 (Tron Network)</MenuItem>
              </Select>
            </FormControl>
          )}

          {walletInfo && (
            <Box
              sx={{ mb: 3, p: 2, border: "1px solid #ddd", borderRadius: 1 }}
            >
              <Typography variant="subtitle1" gutterBottom>
                Our {CURRENCY_INFO[depositData.currency].name} Wallet Address:
              </Typography>
              <TextField
                fullWidth
                value={
                  depositData.currency === "BTC"
                    ? walletInfo.btcAddress
                    : depositData.currency === "ETH"
                    ? walletInfo.ethAddress
                    : walletInfo.usdtAddress
                }
                InputProps={{
                  readOnly: true,
                  endAdornment: (
                    <InputAdornment position="end">
                      <IconButton
                        onClick={() =>
                          copyToClipboard(
                            depositData.currency === "BTC"
                              ? walletInfo.btcAddress
                              : depositData.currency === "ETH"
                              ? walletInfo.ethAddress
                              : walletInfo.usdtAddress,
                            depositData.currency
                          )
                        }
                        edge="end"
                      >
                        <ContentCopyIcon />
                      </IconButton>
                    </InputAdornment>
                  ),
                }}
                sx={{ mb: 1 }}
              />
              {copied[depositData.currency] && (
                <FormHelperText>Copied to clipboard!</FormHelperText>
              )}
              <Box sx={{ display: "flex", gap: 2, mt: 1 }}>
                <Link
                  href={CURRENCY_INFO[depositData.currency].website}
                  target="_blank"
                  rel="noopener"
                >
                  Visit {CURRENCY_INFO[depositData.currency].name} Website
                </Link>
                <Link href={getExplorerLink()} target="_blank" rel="noopener">
                  View on Blockchain Explorer
                </Link>
              </Box>
            </Box>
          )}

          <TextField
            fullWidth
            margin="normal"
            label={`Your ${depositData.currency} Wallet Address`}
            name="walletAddress"
            value={depositData.walletAddress}
            onChange={handleInputChange}
            required
            helperText={
              depositData.walletAddress && !isValidAddress
                ? "Please enter a valid wallet address"
                : ""
            }
            error={depositData.walletAddress.length > 0 && !isValidAddress}
            sx={{ mb: 3 }}
          />

          <TextField
            fullWidth
            margin="normal"
            label="Amount"
            name="amount"
            type="number"
            inputProps={{ min: "0", step: "0.00000001" }}
            value={depositData.amount || ""}
            onChange={handleInputChange}
            required
            sx={{ mb: 3 }}
          />

          <Box sx={{ mb: 3 }}>
            <Button
              component="label"
              variant="outlined"
              startIcon={<CloudUploadIcon />}
              fullWidth
              sx={{ py: 2 }}
            >
              Upload Deposit Confirmation Screenshot
              <VisuallyHiddenInput
                type="file"
                accept="image/*"
                onChange={handleFileChange}
              />
            </Button>
            {screenshot && (
              <Typography variant="body2" sx={{ mt: 1 }}>
                Selected file: {screenshot.name}
              </Typography>
            )}
          </Box>

          <Button
            type="submit"
            variant="contained"
            size="large"
            fullWidth
            sx={{ mt: 2, py: 1.5 }}
            disabled={!isValidAddress || depositData.amount <= 0 || !screenshot}
          >
            Submit Deposit Request
          </Button>
        </Box>

        <Box
          sx={{
            mt: 4,
            p: 3,
            bgcolor: "background.paper",
            borderRadius: 1,
            borderLeft: "4px solid",
            borderColor: "primary.main",
          }}
        >
          <Typography variant="h6" gutterBottom sx={{ fontWeight: "bold" }}>
            Deposit Instructions
          </Typography>
          <Typography variant="body1" sx={{ mt: 1 }}>
            1. Select your cryptocurrency from the dropdown
          </Typography>
          <Typography variant="body1" sx={{ mt: 1 }}>
            2. Copy our wallet address and use it to send funds from your wallet
            or exchange
          </Typography>
          <Typography variant="body1" sx={{ mt: 1 }}>
            3. Enter the amount you deposited and your wallet address
          </Typography>
          <Typography variant="body1" sx={{ mt: 1 }}>
            4. Upload a screenshot of the transaction confirmation
          </Typography>
          <Typography variant="body1" sx={{ mt: 1 }}>
            5. Submit the form for manual verification
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mt: 2 }}>
            <strong>Note:</strong> Deposits may take time to process depending
            on network congestion. Our team will verify your deposit and update
            your balance manually.
          </Typography>
        </Box>
      </Paper>
    </Container>
  );
};

export default DepositForm;
