import React, { useState, useEffect } from "react";
import {
  Box,
  Container,
  Typography,
  Paper,
  Button,
  Divider,
  Tab,
  Tabs,
  Card,
  CardContent,
  TextField,
  InputAdornment,
  CircularProgress,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Chip,
  Fade,
  IconButton,
} from "@mui/material";
import Grid from "@mui/material/Grid";
import {
  AccountBalanceWallet as WalletIcon,
  TrendingUp as TrendingIcon,
  ShowChart as ChartIcon,
  History as HistoryIcon,
  ArrowUpward as GainIcon,
  ArrowDownward as LossIcon,
  CheckCircle as CheckIcon,
  Paid as CryptoIcon,
  Brightness4 as DarkModeIcon,
  Brightness7 as LightModeIcon,
} from "@mui/icons-material";
import { useAuth } from "../context/AuthContext";
import { invest, getInvestmentHistory } from "../api/api";

const investmentPlans = [
  {
    id: 1,
    name: "Venture Plan",
    minAmount: 50,
    maxAmount: 99,
    duration: "15 days",
    dailyReturn: 2.0,
  },
  {
    id: 2,
    name: "Voyager Plan",
    minAmount: 100,
    maxAmount: 249,
    duration: "30 days",
    dailyReturn: 5.0,
  },
  {
    id: 3,
    name: "Pioneer Plan",
    minAmount: 250,
    maxAmount: 499,
    duration: "45 days",
    dailyReturn: 7.5,
  },
  {
    id: 4,
    name: "Catalyst Plan",
    minAmount: 500,
    maxAmount: 999,
    duration: "60 days",
    dailyReturn: 10.0,
  },
  {
    id: 5,
    name: "Legend Plan",
    minAmount: 1000,
    maxAmount: 99999,
    duration: "90 days",
    dailyReturn: 12.5,
  },
];

const cryptoAssets = [
  { id: "btc", name: "Bitcoin", symbol: "BTC" },
  { id: "eth", name: "Ethereum", symbol: "ETH" },
  { id: "usdt", name: "Tether", symbol: "USDT" },
  { id: "sol", name: "Solana", symbol: "SOL" },
  { id: "ada", name: "Cardano", symbol: "ADA" },
];

const InvestPage: React.FC = () => {
  const { user } = useAuth();
  const [activeTab, setActiveTab] = useState(0);
  const [amount, setAmount] = useState("");
  const [selectedPlan, setSelectedPlan] = useState<number | null>(null);
  const [selectedAsset, setSelectedAsset] = useState<string>("USDT");
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [history, setHistory] = useState<any[]>([]);
  const [theme, setTheme] = useState<"dark" | "light">("dark");

  const palette = {
    dark: {
      background: "#121212",
      paper: "#1a1a1a",
      text: "#fff",
      textMuted: "#999",
      border: "#333",
      primary: "#64ffda",
    },
    light: {
      background: "#f0f2f5",
      paper: "#ffffff",
      text: "#333",
      textMuted: "#666",
      border: "#e0e0e0",
      primary: "#00796b", // A darker teal for contrast
    },
  };

  const currentPalette = palette[theme];

  useEffect(() => {
    const fetchHistory = async () => {
      try {
        const data = await getInvestmentHistory();
        setHistory(data);
      } catch (err) {
        console.error("Failed to fetch history:", err);
      }
    };

    if (user) {
      fetchHistory();
    }
  }, [user]);

  const handleInvest = async () => {
    if (!selectedPlan || !amount) {
      setError("Please select a plan and enter an amount.");
      return;
    }
    const investmentAmount = parseFloat(amount);
    const plan = investmentPlans.find((p) => p.id === selectedPlan);
    if (!plan) {
      setError("Invalid investment plan selected.");
      return;
    }
    if (
      investmentAmount < plan.minAmount ||
      investmentAmount > plan.maxAmount
    ) {
      setError(
        `Amount must be between $${plan.minAmount} and $${plan.maxAmount}.`
      );
      return;
    }
    if (user && investmentAmount > (user.balance || 0)) {
      setError("Insufficient balance.");
      return;
    }
    setLoading(true);
    setError("");
    setSuccess("");
    try {
      await invest({
        planId: selectedPlan,
        amount: investmentAmount,
        asset: selectedAsset,
      });
      setSuccess(
        "Investment successful! You can check your history for updates."
      );
      setAmount("");
      setSelectedPlan(null);
    } catch (err) {
      setError(
        err instanceof Error
          ? err.message
          : "Investment failed. Please try again."
      );
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString: string) => {
    if (!dateString) return "";
    return new Date(dateString).toLocaleDateString();
  };

  const selectedPlanDetails = selectedPlan
    ? investmentPlans.find((p) => p.id === selectedPlan)
    : null;

  const totalReturn = selectedPlanDetails
    ? (
        ((parseFloat(amount || "0") * selectedPlanDetails.dailyReturn) / 100) *
        parseFloat(selectedPlanDetails.duration.replace(" days", ""))
      ).toFixed(2)
    : "0.00";

  return (
    <Container
      maxWidth="xl"
      sx={{
        py: 6,
        backgroundColor: currentPalette.background,
        color: currentPalette.text,
        minHeight: "100vh",
        position: "relative",
      }}
    >
      <IconButton
        onClick={() => setTheme(theme === "dark" ? "light" : "dark")}
        sx={{
          position: "absolute",
          top: 16,
          right: 16,
          color: currentPalette.text,
        }}
      >
        {theme === "dark" ? <LightModeIcon /> : <DarkModeIcon />}
      </IconButton>

      <Typography
        variant="h3"
        component="h1"
        sx={{
          mb: 5,
          fontWeight: "bold",
          color: currentPalette.primary,
          textAlign: "center",
          textShadow: `0 0 10px ${currentPalette.primary}40`,
        }}
      >
        Your Investment Dashboard
      </Typography>

      <Grid container spacing={4} sx={{ mb: 6 }}>
        <Grid item xs={12} md={4}>
          <Card
            sx={{
              backgroundColor: currentPalette.paper,
              border: `1px solid ${currentPalette.border}`,
              borderRadius: 3,
              boxShadow:
                theme === "dark"
                  ? "0px 8px 24px rgba(0,0,0,0.5)"
                  : "0px 8px 24px rgba(0,0,0,0.1)",
              height: "100%",
              transition: "transform 0.3s ease",
              "&:hover": { transform: "scale(1.02)" },
            }}
          >
            <CardContent>
              <Box sx={{ display: "flex", alignItems: "center", mb: 1 }}>
                <WalletIcon
                  sx={{ mr: 1.5, color: currentPalette.primary, fontSize: 32 }}
                />
                <Typography
                  variant="h6"
                  sx={{ color: currentPalette.text, fontWeight: "medium" }}
                >
                  Account Balance
                </Typography>
              </Box>
              <Typography
                variant="h3"
                sx={{ fontWeight: "bold", color: currentPalette.primary }}
              >
                ${user?.balance?.toFixed(2) || "0.00"}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={4}>
          <Card
            sx={{
              backgroundColor: currentPalette.paper,
              border: `1px solid ${currentPalette.border}`,
              borderRadius: 3,
              boxShadow:
                theme === "dark"
                  ? "0px 8px 24px rgba(0,0,0,0.5)"
                  : "0px 8px 24px rgba(0,0,0,0.1)",
              height: "100%",
              transition: "transform 0.3s ease",
              "&:hover": { transform: "scale(1.02)" },
            }}
          >
            <CardContent>
              <Box sx={{ display: "flex", alignItems: "center", mb: 1 }}>
                <TrendingIcon
                  sx={{ mr: 1.5, color: currentPalette.primary, fontSize: 32 }}
                />
                <Typography
                  variant="h6"
                  sx={{ color: currentPalette.text, fontWeight: "medium" }}
                >
                  Active Investments
                </Typography>
              </Box>
              <Typography
                variant="h3"
                sx={{ fontWeight: "bold", color: currentPalette.text }}
              >
                $0.00
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={12} md={4}>
          <Card
            sx={{
              backgroundColor: currentPalette.paper,
              border: `1px solid ${currentPalette.border}`,
              borderRadius: 3,
              boxShadow:
                theme === "dark"
                  ? "0px 8px 24px rgba(0,0,0,0.5)"
                  : "0px 8px 24px rgba(0,0,0,0.1)",
              height: "100%",
              transition: "transform 0.3s ease",
              "&:hover": { transform: "scale(1.02)" },
            }}
          >
            <CardContent>
              <Box sx={{ display: "flex", alignItems: "center", mb: 1 }}>
                <ChartIcon
                  sx={{ mr: 1.5, color: currentPalette.primary, fontSize: 32 }}
                />
                <Typography
                  variant="h6"
                  sx={{ color: currentPalette.text, fontWeight: "medium" }}
                >
                  Total Profit
                </Typography>
              </Box>
              <Typography
                variant="h3"
                sx={{ fontWeight: "bold", color: currentPalette.primary }}
              >
                $0.00
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      <Paper
        elevation={3}
        sx={{
          p: 0,
          borderRadius: 3,
          backgroundColor: currentPalette.paper,
          border: `1px solid ${currentPalette.border}`,
        }}
      >
        <Tabs
          value={activeTab}
          onChange={(_, newValue) => setActiveTab(newValue)}
          centered
          sx={{
            borderBottom: 1,
            borderColor: currentPalette.border,
            "& .MuiTabs-indicator": {
              backgroundColor: currentPalette.primary,
            },
          }}
        >
          <Tab
            label="Invest Now"
            icon={<TrendingIcon />}
            sx={{
              color:
                activeTab === 0
                  ? currentPalette.primary
                  : currentPalette.textMuted,
              "&.Mui-selected": {
                color: currentPalette.primary,
              },
            }}
          />
          <Tab
            label="History"
            icon={<HistoryIcon />}
            sx={{
              color:
                activeTab === 1
                  ? currentPalette.primary
                  : currentPalette.textMuted,
              "&.Mui-selected": {
                color: currentPalette.primary,
              },
            }}
          />
        </Tabs>

        <Box sx={{ p: 4 }}>
          {activeTab === 0 ? (
            <Fade in={true} timeout={500}>
              <Box>
                <Typography
                  variant="h5"
                  sx={{ mb: 4, fontWeight: "bold", color: currentPalette.text }}
                >
                  Choose Your Plan
                </Typography>

                <Grid container spacing={4} sx={{ mb: 4 }}>
                  {investmentPlans.map((plan) => (
                    <Grid item xs={12} sm={6} lg={4} key={plan.id}>
                      <Card
                        onClick={() => setSelectedPlan(plan.id)}
                        sx={{
                          cursor: "pointer",
                          backgroundColor: currentPalette.background,
                          border:
                            selectedPlan === plan.id
                              ? `2px solid ${currentPalette.primary}`
                              : `1px solid ${currentPalette.border}`,
                          borderRadius: 3,
                          transition: "border 0.3s ease, transform 0.3s ease",
                          height: "100%",
                          position: "relative",
                          "&:hover": {
                            transform: "scale(1.02)",
                          },
                        }}
                      >
                        <CardContent>
                          {selectedPlan === plan.id && (
                            <CheckIcon
                              sx={{
                                position: "absolute",
                                top: 12,
                                right: 12,
                                color: currentPalette.primary,
                              }}
                            />
                          )}
                          <Typography
                            variant="h5"
                            sx={{
                              fontWeight: "bold",
                              mb: 1,
                              color: currentPalette.text,
                            }}
                          >
                            {plan.name}
                          </Typography>
                          <Typography
                            variant="body2"
                            color={currentPalette.textMuted}
                            sx={{ mb: 2 }}
                          >
                            Duration: **{plan.duration}**
                          </Typography>
                          <Box sx={{ mb: 2 }}>
                            <Typography
                              variant="h6"
                              sx={{
                                color: currentPalette.text,
                                fontWeight: "medium",
                              }}
                            >
                              Daily Return: **{plan.dailyReturn}%**
                            </Typography>
                            <Typography
                              variant="body1"
                              color={currentPalette.textMuted}
                            >
                              Total Return: **
                              {(
                                plan.dailyReturn *
                                parseInt(plan.duration.replace(" days", ""))
                              ).toFixed(2)}
                              %**
                            </Typography>
                          </Box>
                          <Typography
                            variant="body2"
                            color={currentPalette.textMuted}
                          >
                            Invest: **${plan.minAmount}** to **${plan.maxAmount}
                            **
                          </Typography>
                        </CardContent>
                      </Card>
                    </Grid>
                  ))}
                </Grid>

                <Divider
                  sx={{ my: 4, backgroundColor: currentPalette.border }}
                />

                <Typography
                  variant="h5"
                  sx={{ mb: 3, fontWeight: "bold", color: currentPalette.text }}
                >
                  Investment Details
                </Typography>

                <Grid container spacing={4}>
                  <Grid item xs={12} md={6}>
                    <Typography
                      variant="subtitle1"
                      sx={{ mb: 2, color: currentPalette.text }}
                    >
                      Select Asset
                    </Typography>
                    <Box
                      sx={{
                        display: "flex",
                        flexWrap: "wrap",
                        gap: 1,
                        mb: 3,
                      }}
                    >
                      {cryptoAssets.map((asset) => (
                        <Chip
                          key={asset.id}
                          label={`${asset.name} (${asset.symbol})`}
                          onClick={() => setSelectedAsset(asset.symbol)}
                          icon={<CryptoIcon />}
                          sx={{
                            backgroundColor:
                              selectedAsset === asset.symbol
                                ? currentPalette.primary
                                : currentPalette.background,
                            color:
                              selectedAsset === asset.symbol
                                ? theme === "dark"
                                  ? "#121212"
                                  : "#fff"
                                : currentPalette.text,
                            "&:hover": {
                              backgroundColor:
                                selectedAsset === asset.symbol
                                  ? theme === "dark"
                                    ? "#52d1b2"
                                    : "#00564d"
                                  : currentPalette.border,
                            },
                            border: `1px solid ${currentPalette.border}`,
                          }}
                        />
                      ))}
                    </Box>
                  </Grid>

                  <Grid item xs={12} md={6}>
                    <TextField
                      fullWidth
                      label="Investment Amount"
                      type="number"
                      value={amount}
                      onChange={(e) => setAmount(e.target.value)}
                      InputProps={{
                        startAdornment: (
                          <InputAdornment
                            position="start"
                            sx={{ color: currentPalette.textMuted }}
                          >
                            $
                          </InputAdornment>
                        ),
                      }}
                      sx={{
                        mb: 3,
                        "& .MuiInputBase-input": { color: currentPalette.text },
                        "& .MuiInputLabel-root": {
                          color: currentPalette.textMuted,
                        },
                        "& .MuiOutlinedInput-notchedOutline": {
                          borderColor: currentPalette.border,
                        },
                        "&:hover .MuiOutlinedInput-notchedOutline": {
                          borderColor: currentPalette.primary,
                        },
                        "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                          borderColor: currentPalette.primary,
                        },
                      }}
                    />

                    {selectedPlanDetails &&
                      amount &&
                      parseFloat(amount) > 0 && (
                        <Box sx={{ mb: 3 }}>
                          <Typography
                            variant="body1"
                            sx={{ mb: 1, color: currentPalette.text }}
                          >
                            Estimated Daily Profit:{" "}
                            <strong>
                              $
                              {(
                                (parseFloat(amount) *
                                  selectedPlanDetails.dailyReturn) /
                                100
                              ).toFixed(2)}
                            </strong>
                          </Typography>
                          <Typography
                            variant="body1"
                            sx={{ color: currentPalette.text }}
                          >
                            Estimated Total Profit:{" "}
                            <strong>${totalReturn}</strong>
                          </Typography>
                        </Box>
                      )}

                    {error && (
                      <Typography color="error" sx={{ mb: 2 }}>
                        {error}
                      </Typography>
                    )}
                    {success && (
                      <Typography sx={{ color: currentPalette.primary, mb: 2 }}>
                        {success}
                      </Typography>
                    )}

                    <Button
                      variant="contained"
                      size="large"
                      onClick={handleInvest}
                      disabled={
                        loading ||
                        !selectedPlan ||
                        !amount ||
                        parseFloat(amount) <= 0
                      }
                      fullWidth
                      sx={{
                        background: currentPalette.primary,
                        color: theme === "dark" ? "#121212" : "#fff",
                        "&:hover": {
                          background: theme === "dark" ? "#52d1b2" : "#00564d",
                          boxShadow:
                            theme === "dark"
                              ? "0 3px 5px 2px rgba(100, 255, 218, .3)"
                              : "0 3px 5px 2px rgba(0, 121, 107, .3)",
                        },
                        textTransform: "none",
                        py: 1.5,
                        fontWeight: "bold",
                      }}
                    >
                      {loading ? (
                        <>
                          <CircularProgress
                            size={24}
                            sx={{
                              color: theme === "dark" ? "#121212" : "#fff",
                              mr: 1,
                            }}
                          />
                          Processing...
                        </>
                      ) : (
                        "Confirm Investment"
                      )}
                    </Button>
                  </Grid>
                </Grid>
              </Box>
            </Fade>
          ) : (
            <Fade in={true} timeout={500}>
              <Box>
                <Typography
                  variant="h5"
                  sx={{ mb: 3, fontWeight: "bold", color: currentPalette.text }}
                >
                  Investment History
                </Typography>

                {history.length > 0 ? (
                  <TableContainer
                    component={Paper}
                    sx={{
                      backgroundColor: currentPalette.background,
                      border: `1px solid ${currentPalette.border}`,
                    }}
                  >
                    <Table>
                      <TableHead>
                        <TableRow>
                          <TableCell
                            sx={{
                              color: currentPalette.textMuted,
                              fontWeight: "bold",
                            }}
                          >
                            Date
                          </TableCell>
                          <TableCell
                            sx={{
                              color: currentPalette.textMuted,
                              fontWeight: "bold",
                            }}
                          >
                            Plan
                          </TableCell>
                          <TableCell
                            sx={{
                              color: currentPalette.textMuted,
                              fontWeight: "bold",
                            }}
                          >
                            Amount
                          </TableCell>
                          <TableCell
                            sx={{
                              color: currentPalette.textMuted,
                              fontWeight: "bold",
                            }}
                          >
                            Asset
                          </TableCell>
                          <TableCell
                            sx={{
                              color: currentPalette.textMuted,
                              fontWeight: "bold",
                            }}
                          >
                            Status
                          </TableCell>
                          <TableCell
                            sx={{
                              color: currentPalette.textMuted,
                              fontWeight: "bold",
                            }}
                          >
                            Profit
                          </TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                        {history.map((item) => (
                          <TableRow
                            key={item.id}
                            sx={{
                              "&:last-child td, &:last-child th": { border: 0 },
                            }}
                          >
                            <TableCell sx={{ color: currentPalette.text }}>
                              {formatDate(item.createdAt)}
                            </TableCell>
                            <TableCell sx={{ color: currentPalette.text }}>
                              {item.planName}
                            </TableCell>
                            <TableCell sx={{ color: currentPalette.text }}>
                              ${item.amount.toFixed(2)}
                            </TableCell>
                            <TableCell sx={{ color: currentPalette.text }}>
                              {item.asset}
                            </TableCell>
                            <TableCell>
                              <Chip
                                label={item.status}
                                color={
                                  item.status === "active"
                                    ? "info"
                                    : item.status === "completed"
                                    ? "success"
                                    : "default"
                                }
                                size="small"
                                sx={{
                                  backgroundColor:
                                    item.status === "active"
                                      ? currentPalette.primary
                                      : item.status === "completed"
                                      ? "#4caf50"
                                      : currentPalette.border,
                                  color:
                                    item.status === "active"
                                      ? theme === "dark"
                                        ? "#121212"
                                        : "#fff"
                                      : currentPalette.text,
                                }}
                              />
                            </TableCell>
                            <TableCell sx={{ color: currentPalette.text }}>
                              <Box
                                sx={{ display: "flex", alignItems: "center" }}
                              >
                                {item.profit >= 0 ? (
                                  <GainIcon color="success" sx={{ mr: 0.5 }} />
                                ) : (
                                  <LossIcon color="error" sx={{ mr: 0.5 }} />
                                )}
                                ${item.profit.toFixed(2)}
                              </Box>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </TableContainer>
                ) : (
                  <Typography
                    variant="body1"
                    sx={{
                      textAlign: "center",
                      py: 4,
                      color: currentPalette.textMuted,
                    }}
                  >
                    No investment history found. Start your first investment!
                  </Typography>
                )}
              </Box>
            </Fade>
          )}
        </Box>
      </Paper>
    </Container>
  );
};

export default InvestPage;
