import React, { useState } from "react";
import {
  Box,
  Button,
  Typography,
  Container,
  Card,
  CardContent,
  TextField,
  Tooltip,
  Grid,
  Avatar,
  Divider,
  Chip,
  InputAdornment,
  IconButton,
  useTheme,
} from "@mui/material";
import {
  PersonAddAlt1 as PersonAddAlt1Icon,
  ContentCopy as ContentCopyIcon,
  Share as ShareIcon,
  Email as EmailIcon,
  Link as LinkIcon,
  CheckCircle as CheckCircleIcon,
} from "@mui/icons-material";
import { styled } from "@mui/material/styles";
import { motion } from "framer-motion";

const GradientButton = styled(Button)(({ theme }) => ({
  background: `linear-gradient(45deg, ${theme.palette.primary.main} 0%, ${theme.palette.secondary.main} 100%)`,
  color: "white",
  fontWeight: "bold",
  padding: "12px 24px",
  borderRadius: "8px",
  boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
  "&:hover": {
    transform: "translateY(-2px)",
    boxShadow: "0 6px 8px rgba(0, 0, 0, 0.15)",
  },
  transition: "all 0.3s ease",
}));

const StyledCard = styled(Card)(({ theme }) => ({
  borderRadius: "16px",
  boxShadow: "0 8px 24px rgba(0, 0, 0, 0.08)",
  overflow: "hidden",
  position: "relative",
  "&:before": {
    content: '""',
    position: "absolute",
    top: 0,
    left: 0,
    right: 0,
    height: "4px",
    background: `linear-gradient(90deg, ${theme.palette.primary.main}, ${theme.palette.secondary.main})`,
  },
}));

const ReferralPage = () => {
  const theme = useTheme();
  const referralCode = "MYUNIQUECODE123";
  const [copied, setCopied] = useState(false);
  const [showShareOptions, setShowShareOptions] = useState(false);

  const handleCopy = () => {
    try {
      navigator.clipboard.writeText(referralCode);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } catch (err) {
      console.error("Failed to copy text: ", err);
    }
  };

  const handleShare = () => {
    if (navigator.share) {
      navigator.share({
        title: "Join me on this awesome platform!",
        text: `Use my referral code ${referralCode} to get started.`,
        url: window.location.href,
      });
    } else {
      setShowShareOptions(!showShareOptions);
    }
  };

  // Mock data for referrals
  const referrals = [
    {
      name: "John Doe",
      status: "Signed up",
      date: "2023-05-15",
      reward: "$10",
    },
    {
      name: "Jane Smith",
      status: "Verified",
      date: "2023-05-10",
      reward: "$10",
    },
    {
      name: "Alex Johnson",
      status: "Active",
      date: "2023-04-28",
      reward: "$20",
    },
  ];

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <motion.div
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            mb: 4,
          }}
        >
          <Typography variant="h4" component="h1" fontWeight="bold">
            Referral Program
          </Typography>
          <Chip
            label="Earn $10 per referral"
            color="secondary"
            sx={{ fontWeight: "bold", fontSize: "1rem", py: 1.5 }}
          />
        </Box>

        <Grid container spacing={4}>
          <Grid item xs={12} md={8}>
            <StyledCard elevation={0}>
              <CardContent sx={{ p: 4 }}>
                <Box sx={{ textAlign: "center", mb: 3 }}>
                  <Avatar
                    sx={{
                      bgcolor: theme.palette.primary.light,
                      width: 80,
                      height: 80,
                      mx: "auto",
                      mb: 2,
                    }}
                  >
                    <PersonAddAlt1Icon sx={{ fontSize: 40 }} />
                  </Avatar>
                  <Typography
                    variant="h5"
                    component="p"
                    mb={2}
                    fontWeight="bold"
                  >
                    Earn Together With Friends
                  </Typography>
                  <Typography variant="body1" color="text.secondary">
                    Share your unique referral link and earn rewards when your
                    friends sign up and complete their first transaction.
                  </Typography>
                </Box>

                <Box sx={{ my: 4 }}>
                  <Typography variant="subtitle1" fontWeight="medium" mb={1}>
                    Your Referral Code
                  </Typography>
                  <Box sx={{ display: "flex", gap: 2 }}>
                    <TextField
                      fullWidth
                      variant="outlined"
                      value={referralCode}
                      InputProps={{
                        readOnly: true,
                        sx: { fontWeight: "bold", fontSize: "1.1rem" },
                        endAdornment: (
                          <InputAdornment position="end">
                            <IconButton onClick={handleCopy} color="primary">
                              <ContentCopyIcon />
                            </IconButton>
                          </InputAdornment>
                        ),
                      }}
                      sx={{
                        "& .MuiOutlinedInput-root": {
                          bgcolor: "background.default",
                          borderRadius: "8px",
                        },
                      }}
                    />
                    <GradientButton onClick={handleCopy}>
                      {copied ? (
                        <>
                          <CheckCircleIcon sx={{ mr: 1 }} />
                          Copied!
                        </>
                      ) : (
                        "Copy Code"
                      )}
                    </GradientButton>
                  </Box>
                </Box>

                <Divider sx={{ my: 3 }} />

                <Box>
                  <Typography variant="subtitle1" fontWeight="medium" mb={2}>
                    Share Via
                  </Typography>
                  <Box sx={{ display: "flex", gap: 2 }}>
                    <Button
                      variant="outlined"
                      startIcon={<EmailIcon />}
                      sx={{ flex: 1, py: 1.5 }}
                    >
                      Email
                    </Button>
                    <Button
                      variant="outlined"
                      startIcon={<LinkIcon />}
                      sx={{ flex: 1, py: 1.5 }}
                      onClick={handleShare}
                    >
                      Share Link
                    </Button>
                  </Box>
                </Box>
              </CardContent>
            </StyledCard>
          </Grid>

          <Grid item xs={12} md={4}>
            <StyledCard elevation={0}>
              <CardContent sx={{ p: 4 }}>
                <Typography variant="h6" fontWeight="bold" mb={3}>
                  How It Works
                </Typography>
                <Box sx={{ mb: 3 }}>
                  <Box sx={{ display: "flex", mb: 2 }}>
                    <Avatar
                      sx={{
                        bgcolor: "primary.main",
                        color: "white",
                        width: 32,
                        height: 32,
                        mr: 2,
                        fontSize: "1rem",
                      }}
                    >
                      1
                    </Avatar>
                    <Box>
                      <Typography fontWeight="medium">
                        Share Your Code
                      </Typography>
                      <Typography variant="body2" color="text.secondary">
                        Send your unique referral code to friends
                      </Typography>
                    </Box>
                  </Box>
                  <Box sx={{ display: "flex", mb: 2 }}>
                    <Avatar
                      sx={{
                        bgcolor: "primary.main",
                        color: "white",
                        width: 32,
                        height: 32,
                        mr: 2,
                        fontSize: "1rem",
                      }}
                    >
                      2
                    </Avatar>
                    <Box>
                      <Typography fontWeight="medium">They Sign Up</Typography>
                      <Typography variant="body2" color="text.secondary">
                        Your friends register using your code
                      </Typography>
                    </Box>
                  </Box>
                  <Box sx={{ display: "flex" }}>
                    <Avatar
                      sx={{
                        bgcolor: "primary.main",
                        color: "white",
                        width: 32,
                        height: 32,
                        mr: 2,
                        fontSize: "1rem",
                      }}
                    >
                      3
                    </Avatar>
                    <Box>
                      <Typography fontWeight="medium">You Both Earn</Typography>
                      <Typography variant="body2" color="text.secondary">
                        Get rewarded when they complete requirements
                      </Typography>
                    </Box>
                  </Box>
                </Box>

                <Divider sx={{ my: 3 }} />

                <Box>
                  <Typography variant="h6" fontWeight="bold" mb={2}>
                    Your Rewards
                  </Typography>
                  <Box
                    sx={{
                      bgcolor: "background.default",
                      borderRadius: "12px",
                      p: 3,
                      textAlign: "center",
                    }}
                  >
                    <Typography variant="h4" fontWeight="bold" color="primary">
                      $40
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      Total earnings from referrals
                    </Typography>
                  </Box>
                </Box>
              </CardContent>
            </StyledCard>
          </Grid>
        </Grid>

        <Box sx={{ mt: 6 }}>
          <Typography variant="h5" component="h2" fontWeight="bold" mb={3}>
            Your Referrals ({referrals.length})
          </Typography>
          <StyledCard elevation={0}>
            <CardContent sx={{ p: 0 }}>
              {referrals.length > 0 ? (
                <Box>
                  <Box
                    sx={{
                      display: "flex",
                      p: 3,
                      borderBottom: "1px solid",
                      borderColor: "divider",
                      fontWeight: "medium",
                    }}
                  >
                    <Box sx={{ flex: 2 }}>Name</Box>
                    <Box sx={{ flex: 1 }}>Status</Box>
                    <Box sx={{ flex: 1 }}>Date</Box>
                    <Box sx={{ flex: 1, textAlign: "right" }}>Reward</Box>
                  </Box>
                  {referrals.map((referral, index) => (
                    <Box
                      key={index}
                      sx={{
                        display: "flex",
                        p: 3,
                        borderBottom:
                          index === referrals.length - 1 ? "none" : "1px solid",
                        borderColor: "divider",
                        alignItems: "center",
                      }}
                    >
                      <Box sx={{ flex: 2, fontWeight: "medium" }}>
                        {referral.name}
                      </Box>
                      <Box sx={{ flex: 1 }}>
                        <Chip
                          label={referral.status}
                          size="small"
                          color={
                            referral.status === "Active"
                              ? "success"
                              : referral.status === "Verified"
                              ? "info"
                              : "default"
                          }
                        />
                      </Box>
                      <Box sx={{ flex: 1 }}>{referral.date}</Box>
                      <Box
                        sx={{ flex: 1, textAlign: "right", fontWeight: "bold" }}
                      >
                        {referral.reward}
                      </Box>
                    </Box>
                  ))}
                </Box>
              ) : (
                <Box sx={{ p: 4, textAlign: "center" }}>
                  <Typography variant="body1" color="text.secondary" mb={2}>
                    You haven't referred anyone yet.
                  </Typography>
                  <Button variant="contained" startIcon={<ShareIcon />}>
                    Start Sharing
                  </Button>
                </Box>
              )}
            </CardContent>
          </StyledCard>
        </Box>
      </motion.div>
    </Container>
  );
};

export default ReferralPage;
