export const validateBTCAddress = (address: string): boolean => {
  // Basic BTC address validation (simplified for demo)
  return /^(bc1|[13])[a-zA-HJ-NP-Z0-9]{25,39}$/.test(address);
};

export const validateETHAddress = (address: string): boolean => {
  // Basic ETH address validation
  return /^0x[a-fA-F0-9]{40}$/.test(address);
};

export const validateUSDTAddress = (
  address: string,
  network: "ERC20" | "TRC20"
): boolean => {
  // USDT can be on different networks
  return network === "ERC20"
    ? validateETHAddress(address)
    : /^T[a-zA-HJ-NP-Z0-9]{33}$/.test(address); // TRC20 pattern
};
