import React, { useState, useEffect } from "react";
import {
  Box,
  Container,
  Typography,
  Avatar,
  Paper,
  TextField,
  Button,
  Divider,
  Grid,
  IconButton,
  InputAdornment,
  CircularProgress,
} from "@mui/material";
import {
  Edit as EditIcon,
  Check as CheckIcon,
  Email as EmailIcon,
  Phone as PhoneIcon,
  Person as PersonIcon,
  Lock as LockIcon,
  Visibility,
  VisibilityOff,
} from "@mui/icons-material";
import { useAuth } from "../context/AuthContext";
import { updateUser as apiUpdateUser } from "../api/api"; // You'll need to implement this API call

const ProfilePage: React.FC = () => {
  const { user, isAuthenticated, logout } = useAuth();
  const [editMode, setEditMode] = useState(false);
  const [showPassword, setShowPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");
  const [formData, setFormData] = useState({
    username: "",
    email: "",
    phone: "",
    password: "",
    referralCode: "",
  });

  useEffect(() => {
    if (user) {
      setFormData({
        username: user.username || "",
        email: user.email || "",
        phone: user.joinedAt || "",
        password: "",
        referralCode: user.referralCode || "",
      });
    }
  }, [user]);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    try {
      // Call your API endpoint to update user
      const updatedUser = await apiUpdateUser(formData);
      // In a real app, you would update the auth context with new user data
      setEditMode(false);
    } catch (err) {
      setError(err instanceof Error ? err.message : "Failed to update profile");
    } finally {
      setLoading(false);
    }
  };

  if (!isAuthenticated || !user) {
    return (
      <Container maxWidth="md" sx={{ py: 4, textAlign: "center" }}>
        <Typography variant="h6">Please log in to view your profile</Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Paper elevation={3} sx={{ p: 4, borderRadius: 2 }}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            mb: 4,
          }}
        >
          <Typography variant="h4" component="h1" sx={{ fontWeight: "bold" }}>
            My Profile
          </Typography>
          <Box>
            <Button
              startIcon={editMode ? <CheckIcon /> : <EditIcon />}
              variant="outlined"
              onClick={editMode ? handleSubmit : () => setEditMode(true)}
              sx={{ textTransform: "none", mr: 2 }}
              disabled={loading}
            >
              {editMode ? "Save Changes" : "Edit Profile"}
              {loading && <CircularProgress size={24} sx={{ ml: 1 }} />}
            </Button>
            <Button
              variant="outlined"
              color="error"
              onClick={logout}
              sx={{ textTransform: "none" }}
            >
              Logout
            </Button>
          </Box>
        </Box>

        {error && (
          <Typography color="error" sx={{ mb: 2 }}>
            {error}
          </Typography>
        )}

        <Grid container spacing={4}>
          {/* Left Column - Profile Info */}
          <Grid item xs={12} md={6}>
            <Box
              sx={{
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                mb: 3,
              }}
            >
              <Avatar
                sx={{ width: 120, height: 120, mb: 2, bgcolor: "#64ffda" }}
              >
                <PersonIcon sx={{ fontSize: 60 }} />
              </Avatar>
              {editMode ? (
                <TextField
                  fullWidth
                  label="Username"
                  name="username"
                  value={formData.username}
                  onChange={handleChange}
                  sx={{ mb: 2 }}
                  InputProps={{
                    startAdornment: (
                      <InputAdornment position="start">
                        <PersonIcon />
                      </InputAdornment>
                    ),
                  }}
                />
              ) : (
                <Typography variant="h5" sx={{ fontWeight: "medium" }}>
                  {user.username}
                </Typography>
              )}
            </Box>

            <Divider sx={{ my: 2 }} />

            <Box sx={{ mb: 3 }}>
              <Typography
                variant="h6"
                sx={{ mb: 2, fontWeight: "bold", color: "#64ffda" }}
              >
                Account Information
              </Typography>

              {editMode ? (
                <>
                  <TextField
                    fullWidth
                    label="Email"
                    name="email"
                    type="email"
                    value={formData.email}
                    onChange={handleChange}
                    sx={{ mb: 2 }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <EmailIcon />
                        </InputAdornment>
                      ),
                    }}
                  />
                  <TextField
                    fullWidth
                    label="Phone"
                    name="phone"
                    value={formData.phone}
                    onChange={handleChange}
                    sx={{ mb: 2 }}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <PhoneIcon />
                        </InputAdornment>
                      ),
                    }}
                  />
                  <TextField
                    fullWidth
                    label="New Password"
                    name="password"
                    type={showPassword ? "text" : "password"}
                    value={formData.password}
                    onChange={handleChange}
                    helperText="Leave blank to keep current password"
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <LockIcon />
                        </InputAdornment>
                      ),
                      endAdornment: (
                        <InputAdornment position="end">
                          <IconButton
                            onClick={() => setShowPassword(!showPassword)}
                            edge="end"
                          >
                            {showPassword ? <VisibilityOff /> : <Visibility />}
                          </IconButton>
                        </InputAdornment>
                      ),
                    }}
                  />
                </>
              ) : (
                <>
                  <Box sx={{ display: "flex", alignItems: "center", mb: 1.5 }}>
                    <EmailIcon sx={{ mr: 1.5, color: "#64ffda" }} />
                    <Typography>{user.email}</Typography>
                  </Box>
                  <Box sx={{ display: "flex", alignItems: "center", mb: 1.5 }}>
                    <PhoneIcon sx={{ mr: 1.5, color: "#64ffda" }} />
                    <Typography>{user.joinedAt || "Not provided"}</Typography>
                  </Box>
                </>
              )}
            </Box>
          </Grid>

          {/* Right Column - Stats & Referral */}
          <Grid item xs={12} md={6}>
            <Box sx={{ mb: 4 }}>
              <Typography
                variant="h6"
                sx={{ mb: 2, fontWeight: "bold", color: "#64ffda" }}
              >
                Investment Stats
              </Typography>
              <Paper
                elevation={0}
                sx={{
                  p: 2,
                  mb: 2,
                  backgroundColor: "rgba(100, 255, 218, 0.1)",
                }}
              >
                <Typography variant="body1" sx={{ mb: 1 }}>
                  Account Balance:{" "}
                  <strong>${user.balance?.toFixed(2) || "0.00"}</strong>
                </Typography>
                <Typography variant="body1">
                  ROI: <strong>{user.roi?.toFixed(2) || "0.00"}%</strong>
                </Typography>
              </Paper>
              <Typography variant="body2" sx={{ fontStyle: "italic" }}>
                Member since: {new Date(user.joinedAt).toLocaleDateString()}
              </Typography>
            </Box>

            <Divider sx={{ my: 2 }} />

            <Box>
              <Typography
                variant="h6"
                sx={{ mb: 2, fontWeight: "bold", color: "#64ffda" }}
              >
                Referral Program
              </Typography>
              {editMode ? (
                <TextField
                  fullWidth
                  label="Referral Code"
                  name="referralCode"
                  value={formData.referralCode}
                  onChange={handleChange}
                  sx={{ mb: 2 }}
                />
              ) : (
                <>
                  <Typography sx={{ mb: 1.5 }}>
                    Your Code:{" "}
                    <strong>{user.referralCode || "Not generated"}</strong>
                  </Typography>
                  <Typography sx={{ mb: 1.5 }}>
                    Referred by: <strong>{user.referredBy || "No one"}</strong>
                  </Typography>
                </>
              )}
              <Button
                variant="contained"
                sx={{
                  backgroundColor: "#64ffda",
                  color: "#121212",
                  "&:hover": { backgroundColor: "#52d1b2" },
                  textTransform: "none",
                }}
                onClick={() => {
                  navigator.clipboard.writeText(
                    `${window.location.origin}/signup?ref=${user.referralCode}`
                  );
                  // Add toast notification in a real app
                }}
              >
                Copy Referral Link
              </Button>
            </Box>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  );
};

export default ProfilePage;
