import React, { useState, useEffect } from "react";
import { useAuth } from "../context/AuthContext";
import { withdrawFunds, getWalletAddresses } from "../api/api";
import { WithdrawalData, WalletInfo } from "../types/User";
import {
  validateBTCAddress,
  validateETHAddress,
  validateUSDTAddress,
} from "../utils/walletValidation";
import {
  Box,
  Typography,
  TextField,
  Button,
  Select,
  MenuItem,
  InputLabel,
  FormControl,
  Paper,
  Container,
  Alert,
  Divider,
  CircularProgress,
} from "@mui/material";
import { SelectChangeEvent } from "@mui/material/Select";

const WithdrawalForm: React.FC = () => {
  const { user, token } = useAuth();
  const [withdrawalData, setWithdrawalData] = useState<WithdrawalData>({
    amount: 0,
    currency: "BTC",
    walletAddress: "",
  });
  const [walletInfo, setWalletInfo] = useState<WalletInfo | null>(null);
  const [network, setNetwork] = useState<"ERC20" | "TRC20">("ERC20");
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");
  const [isValidAddress, setIsValidAddress] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [availableBalance, setAvailableBalance] = useState(0);

  useEffect(() => {
    const fetchWalletInfo = async () => {
      if (user && token) {
        try {
          setIsLoading(true);
          const addresses = await getWalletAddresses(user.id, token);
          setWalletInfo(addresses);
          // Set available balance from user data
          setAvailableBalance(user.balance);
        } catch (err) {
          setError("Failed to load wallet information");
        } finally {
          setIsLoading(false);
        }
      }
    };

    fetchWalletInfo();
  }, [user, token]);

  useEffect(() => {
    if (!withdrawalData.walletAddress) {
      setIsValidAddress(false);
      return;
    }

    try {
      let valid = false;
      switch (withdrawalData.currency) {
        case "BTC":
          valid = validateBTCAddress(withdrawalData.walletAddress);
          break;
        case "ETH":
          valid = validateETHAddress(withdrawalData.walletAddress);
          break;
        case "USDT":
          valid = validateUSDTAddress(withdrawalData.walletAddress, network);
          break;
      }
      setIsValidAddress(valid);
    } catch (err) {
      setIsValidAddress(false);
    }
  }, [withdrawalData.walletAddress, withdrawalData.currency, network]);

  const handleCurrencyChange = (
    event: SelectChangeEvent<"BTC" | "ETH" | "USDT">
  ) => {
    setWithdrawalData((prev) => ({
      ...prev,
      currency: event.target.value as "BTC" | "ETH" | "USDT",
      walletAddress: "", // Reset wallet address when currency changes
    }));
  };

  const handleNetworkChange = (event: SelectChangeEvent<"ERC20" | "TRC20">) => {
    setNetwork(event.target.value as "ERC20" | "TRC20");
    if (withdrawalData.currency === "USDT") {
      setIsValidAddress(false); // Force revalidation of address
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setWithdrawalData((prev) => ({
      ...prev,
      [name]: name === "amount" ? parseFloat(value) || 0 : value,
    }));
  };

  const handleMaxAmount = () => {
    setWithdrawalData((prev) => ({
      ...prev,
      amount: availableBalance,
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError("");
    setSuccess("");

    if (!isValidAddress) {
      setError("Please enter a valid wallet address");
      return;
    }

    if (withdrawalData.amount <= 0) {
      setError("Amount must be greater than 0");
      return;
    }

    if (withdrawalData.amount > availableBalance) {
      setError("Amount exceeds available balance");
      return;
    }

    try {
      setIsLoading(true);
      if (user && token) {
        const withdrawalPayload =
          withdrawalData.currency === "USDT"
            ? { ...withdrawalData, network }
            : withdrawalData;

        await withdrawFunds(user.id, withdrawalPayload, token);
        setSuccess(
          `Withdrawal of ${withdrawalData.amount} ${withdrawalData.currency} requested successfully!`
        );

        // Reset form but keep the same currency
        setWithdrawalData((prev) => ({
          amount: 0,
          currency: prev.currency,
          walletAddress: "",
        }));

        // Update available balance
        setAvailableBalance((prev) => prev - withdrawalData.amount);
      }
    } catch (err) {
      setError(
        err instanceof Error
          ? err.message
          : "Withdrawal failed. Please try again."
      );
    } finally {
      setIsLoading(false);
    }
  };

  if (isLoading && !walletInfo) {
    return (
      <Container
        maxWidth="md"
        sx={{ display: "flex", justifyContent: "center", mt: 4 }}
      >
        <CircularProgress />
      </Container>
    );
  }

  return (
    <Container maxWidth="md" sx={{ py: 4 }}>
      <Paper elevation={3} sx={{ p: 4 }}>
        <Typography
          variant="h4"
          component="h1"
          gutterBottom
          sx={{ fontWeight: "bold" }}
        >
          Request Withdrawal
        </Typography>
        <Divider sx={{ my: 3 }} />

        <Box sx={{ mb: 3, p: 2, bgcolor: "background.paper", borderRadius: 1 }}>
          <Typography variant="h6" sx={{ mb: 1 }}>
            Available Balance: {availableBalance.toFixed(8)}{" "}
            {withdrawalData.currency}
          </Typography>
          <Button
            variant="outlined"
            size="small"
            onClick={handleMaxAmount}
            sx={{ mt: 1 }}
          >
            Use Max Amount
          </Button>
        </Box>

        {error && (
          <Alert severity="error" sx={{ mb: 3 }}>
            {error}
          </Alert>
        )}
        {success && (
          <Alert severity="success" sx={{ mb: 3 }}>
            {success}
          </Alert>
        )}

        <Box component="form" onSubmit={handleSubmit} noValidate>
          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel id="currency-label">Currency</InputLabel>
            <Select
              labelId="currency-label"
              id="currency"
              value={withdrawalData.currency}
              label="Currency"
              onChange={handleCurrencyChange}
            >
              <MenuItem value="BTC">Bitcoin (BTC)</MenuItem>
              <MenuItem value="ETH">Ethereum (ETH)</MenuItem>
              <MenuItem value="USDT">Tether (USDT)</MenuItem>
            </Select>
          </FormControl>

          {withdrawalData.currency === "USDT" && (
            <FormControl fullWidth sx={{ mb: 3 }}>
              <InputLabel id="network-label">Network</InputLabel>
              <Select
                labelId="network-label"
                value={network}
                label="Network"
                onChange={handleNetworkChange}
              >
                <MenuItem value="ERC20">ERC20 (Ethereum Network)</MenuItem>
                <MenuItem value="TRC20">TRC20 (Tron Network)</MenuItem>
              </Select>
            </FormControl>
          )}

          <TextField
            fullWidth
            margin="normal"
            label={`Your ${withdrawalData.currency} Withdrawal Address`}
            name="walletAddress"
            value={withdrawalData.walletAddress}
            onChange={handleInputChange}
            required
            helperText={
              withdrawalData.walletAddress && !isValidAddress
                ? "Please enter a valid wallet address"
                : ""
            }
            error={withdrawalData.walletAddress.length > 0 && !isValidAddress}
            sx={{ mb: 3 }}
          />

          <TextField
            fullWidth
            margin="normal"
            label="Amount"
            name="amount"
            type="number"
            inputProps={{
              min: "0",
              step: "0.00000001",
              max: availableBalance,
            }}
            value={withdrawalData.amount || ""}
            onChange={handleInputChange}
            required
            sx={{ mb: 3 }}
          />

          <Button
            type="submit"
            variant="contained"
            size="large"
            fullWidth
            sx={{ mt: 2, py: 1.5 }}
            disabled={
              !isValidAddress || withdrawalData.amount <= 0 || isLoading
            }
          >
            {isLoading ? <CircularProgress size={24} /> : "Request Withdrawal"}
          </Button>
        </Box>

        {walletInfo && (
          <Box
            sx={{
              mt: 4,
              p: 3,
              bgcolor: "background.paper",
              borderRadius: 1,
              borderLeft: "4px solid",
              borderColor: "warning.main",
            }}
          >
            <Typography variant="h6" gutterBottom sx={{ fontWeight: "bold" }}>
              Important Information
            </Typography>
            <Typography variant="body1" sx={{ mt: 2 }}>
              <strong>Processing Time:</strong> 1-3 business days
            </Typography>
            <Typography variant="body1" sx={{ mt: 1 }}>
              <strong>Minimum Withdrawal:</strong> 0.001{" "}
              {withdrawalData.currency}
            </Typography>
            <Typography variant="body1" sx={{ mt: 1 }}>
              <strong>Network Fees:</strong> Will be deducted from your
              withdrawal amount
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 2 }}>
              <strong>Note:</strong> Double-check your withdrawal address.
              Transactions cannot be reversed.
            </Typography>
          </Box>
        )}
      </Paper>
    </Container>
  );
};

export default WithdrawalForm;
